"use client"

import { useState } from "react"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"
import { useLanguage } from "@/contexts/language-context"
import { useAISettings, type AIProvider } from "@/contexts/ai-settings-context"
import { useTheme } from "next-themes"

interface SettingsDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
}

export function SettingsDialog({ open, onOpenChange }: SettingsDialogProps) {
  const { t, language, setLanguage } = useLanguage()
  const { theme, setTheme } = useTheme()
  const { settings, updateSettings, updateApiKey, getModelsForProvider, testConnection } = useAISettings()
  const [activeTab, setActiveTab] = useState<AIProvider>(settings.selectedProvider)
  const [testing, setTesting] = useState<AIProvider | null>(null)

  const handleProviderChange = (provider: AIProvider) => {
    setActiveTab(provider)
    updateSettings({ selectedProvider: provider })
    
    // Auto-select first model for the provider
    const models = getModelsForProvider(provider)
    if (models.length > 0) {
      updateSettings({ selectedModel: models[0].id })
    }
  }

  const handleTestConnection = async (provider: AIProvider) => {
    setTesting(provider)
    await testConnection(provider)
    setTesting(null)
  }

  const handleSaveSettings = () => {
    onOpenChange(false)
  }

  const providerTabs = [
    { id: 'gemini' as AIProvider, label: 'Gemini', color: 'bg-blue-600' },
    { id: 'openrouter' as AIProvider, label: 'OpenRouter', color: 'bg-purple-600' },
    { id: 'openai' as AIProvider, label: 'OpenAI', color: 'bg-green-600' },
    { id: 'anthropic' as AIProvider, label: 'Anthropic', color: 'bg-orange-600' },
    { id: 'local' as AIProvider, label: 'Local', color: 'bg-gray-600' },
  ]

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[600px] max-h-[80vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="text-xl font-semibold">{t('settingsTitle')}</DialogTitle>
        </DialogHeader>
        
        <div className="space-y-6">
          {/* AI Provider Tabs */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium">AI Provider</h3>
            <Tabs value={activeTab} onValueChange={(value) => handleProviderChange(value as AIProvider)}>
              <TabsList className="grid w-full grid-cols-5 gap-1">
                {providerTabs.map((tab) => (
                  <TabsTrigger
                    key={tab.id}
                    value={tab.id}
                    className={`text-xs ${
                      activeTab === tab.id ? tab.color + ' text-white' : ''
                    }`}
                  >
                    {tab.label}
                  </TabsTrigger>
                ))}
              </TabsList>

              {/* Gemini Tab */}
              <TabsContent value="gemini" className="space-y-4">
                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <Label htmlFor="gemini-key">Google Gemini API Key</Label>
                    <div className="flex items-center gap-2">
                      <Badge variant={settings.connectionStatus.gemini ? "default" : "secondary"}>
                        {settings.connectionStatus.gemini ? "Connected" : "Not connected"}
                      </Badge>
                    </div>
                  </div>
                  <Input
                    id="gemini-key"
                    type="password"
                    placeholder="Enter your Gemini API key..."
                    value={settings.apiKeys.gemini}
                    onChange={(e) => updateApiKey('gemini', e.target.value)}
                  />
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleTestConnection('gemini')}
                    disabled={testing === 'gemini' || !settings.apiKeys.gemini}
                  >
                    {testing === 'gemini' ? 'Testing...' : 'Test Connection'}
                  </Button>
                </div>
                
                <div className="space-y-3">
                  <Label>Model</Label>
                  <div className="space-y-2">
                    {getModelsForProvider('gemini').map((model) => (
                      <div
                        key={model.id}
                        className={`p-3 rounded-lg border cursor-pointer transition-colors ${
                          settings.selectedModel === model.id
                            ? 'border-red-500 bg-red-50 dark:bg-red-950'
                            : 'border-border hover:bg-muted'
                        }`}
                        onClick={() => updateSettings({ selectedModel: model.id })}
                      >
                        <div className="font-medium">{model.name}</div>
                        <div className="text-sm text-muted-foreground">{model.description}</div>
                      </div>
                    ))}
                  </div>
                </div>
              </TabsContent>

              {/* OpenRouter Tab */}
              <TabsContent value="openrouter" className="space-y-4">
                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <Label htmlFor="openrouter-key">OpenRouter API Key</Label>
                    <Badge variant={settings.connectionStatus.openrouter ? "default" : "secondary"}>
                      {settings.connectionStatus.openrouter ? "Connected" : "Not connected"}
                    </Badge>
                  </div>
                  <Input
                    id="openrouter-key"
                    type="password"
                    placeholder="Enter your OpenRouter API key..."
                    value={settings.apiKeys.openrouter}
                    onChange={(e) => updateApiKey('openrouter', e.target.value)}
                  />
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleTestConnection('openrouter')}
                    disabled={testing === 'openrouter' || !settings.apiKeys.openrouter}
                  >
                    {testing === 'openrouter' ? 'Testing...' : 'Test Connection'}
                  </Button>
                </div>
                
                <div className="space-y-3">
                  <Label>Model</Label>
                  <div className="space-y-2">
                    {getModelsForProvider('openrouter').map((model) => (
                      <div
                        key={model.id}
                        className={`p-3 rounded-lg border cursor-pointer transition-colors ${
                          settings.selectedModel === model.id
                            ? 'border-purple-500 bg-purple-50 dark:bg-purple-950'
                            : 'border-border hover:bg-muted'
                        }`}
                        onClick={() => updateSettings({ selectedModel: model.id })}
                      >
                        <div className="font-medium">{model.name}</div>
                        <div className="text-sm text-muted-foreground">{model.description}</div>
                      </div>
                    ))}
                  </div>
                </div>
              </TabsContent>

              {/* OpenAI Tab */}
              <TabsContent value="openai" className="space-y-4">
                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <Label htmlFor="openai-key">OpenAI API Key</Label>
                    <Badge variant={settings.connectionStatus.openai ? "default" : "secondary"}>
                      {settings.connectionStatus.openai ? "Connected" : "Not connected"}
                    </Badge>
                  </div>
                  <Input
                    id="openai-key"
                    type="password"
                    placeholder="Enter your OpenAI API key..."
                    value={settings.apiKeys.openai}
                    onChange={(e) => updateApiKey('openai', e.target.value)}
                  />
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleTestConnection('openai')}
                    disabled={testing === 'openai' || !settings.apiKeys.openai}
                  >
                    {testing === 'openai' ? 'Testing...' : 'Test Connection'}
                  </Button>
                </div>
                
                <div className="space-y-3">
                  <Label>Model</Label>
                  <div className="space-y-2">
                    {getModelsForProvider('openai').map((model) => (
                      <div
                        key={model.id}
                        className={`p-3 rounded-lg border cursor-pointer transition-colors ${
                          settings.selectedModel === model.id
                            ? 'border-green-500 bg-green-50 dark:bg-green-950'
                            : 'border-border hover:bg-muted'
                        }`}
                        onClick={() => updateSettings({ selectedModel: model.id })}
                      >
                        <div className="font-medium">{model.name}</div>
                        <div className="text-sm text-muted-foreground">{model.description}</div>
                      </div>
                    ))}
                  </div>
                </div>
              </TabsContent>

              {/* Anthropic Tab */}
              <TabsContent value="anthropic" className="space-y-4">
                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <Label htmlFor="anthropic-key">Anthropic API Key</Label>
                    <Badge variant={settings.connectionStatus.anthropic ? "default" : "secondary"}>
                      {settings.connectionStatus.anthropic ? "Connected" : "Not connected"}
                    </Badge>
                  </div>
                  <Input
                    id="anthropic-key"
                    type="password"
                    placeholder="Enter your Anthropic API key..."
                    value={settings.apiKeys.anthropic}
                    onChange={(e) => updateApiKey('anthropic', e.target.value)}
                  />
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleTestConnection('anthropic')}
                    disabled={testing === 'anthropic' || !settings.apiKeys.anthropic}
                  >
                    {testing === 'anthropic' ? 'Testing...' : 'Test Connection'}
                  </Button>
                </div>
                
                <div className="space-y-3">
                  <Label>Model</Label>
                  <div className="space-y-2">
                    {getModelsForProvider('anthropic').map((model) => (
                      <div
                        key={model.id}
                        className={`p-3 rounded-lg border cursor-pointer transition-colors ${
                          settings.selectedModel === model.id
                            ? 'border-orange-500 bg-orange-50 dark:bg-orange-950'
                            : 'border-border hover:bg-muted'
                        }`}
                        onClick={() => updateSettings({ selectedModel: model.id })}
                      >
                        <div className="font-medium">{model.name}</div>
                        <div className="text-sm text-muted-foreground">{model.description}</div>
                      </div>
                    ))}
                  </div>
                </div>
              </TabsContent>

              {/* Local Tab */}
              <TabsContent value="local" className="space-y-4">
                <div className="space-y-3">
                  <div className="flex items-center justify-between">
                    <Label>Local Models</Label>
                    <Badge variant="default">Always Available</Badge>
                  </div>
                  <p className="text-sm text-muted-foreground">
                    Local models run on your device and don't require an API key.
                  </p>
                </div>
                
                <div className="space-y-3">
                  <Label>Model</Label>
                  <div className="space-y-2">
                    {getModelsForProvider('local').map((model) => (
                      <div
                        key={model.id}
                        className={`p-3 rounded-lg border cursor-pointer transition-colors ${
                          settings.selectedModel === model.id
                            ? 'border-gray-500 bg-gray-50 dark:bg-gray-950'
                            : 'border-border hover:bg-muted'
                        }`}
                        onClick={() => updateSettings({ selectedModel: model.id })}
                      >
                        <div className="font-medium">{model.name}</div>
                        <div className="text-sm text-muted-foreground">{model.description}</div>
                      </div>
                    ))}
                  </div>
                </div>
              </TabsContent>
            </Tabs>
          </div>

          <Separator />

          {/* General Settings */}
          <div className="space-y-4">
            <h3 className="text-lg font-medium">General Settings</h3>
            
            {/* Theme Selection */}
            <div className="grid gap-2">
              <Label htmlFor="theme">{t('theme')}</Label>
              <Select value={theme} onValueChange={setTheme}>
                <SelectTrigger>
                  <SelectValue placeholder={t('theme')} />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="light">{t('lightTheme')}</SelectItem>
                  <SelectItem value="dark">{t('darkTheme')}</SelectItem>
                  <SelectItem value="system">{t('systemTheme')}</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* Language Selection */}
            <div className="grid gap-2">
              <Label htmlFor="language">{t('language')}</Label>
              <Select value={language} onValueChange={(value: 'en' | 'id') => setLanguage(value)}>
                <SelectTrigger>
                  <SelectValue placeholder={t('language')} />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="en">{t('english')}</SelectItem>
                  <SelectItem value="id">{t('indonesian')}</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Save Button */}
          <div className="flex justify-end pt-4">
            <Button onClick={handleSaveSettings} className="bg-red-600 hover:bg-red-700 text-white">
              Save Settings
            </Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}
